@import url("https://fonts.googleapis.com/css?family=Open+Sans");
.cl-title {
  font-weight: 600;
}

h1.cl-title {
  text-transform: uppercase;
  font-size: 1.5em;
}

.-user-title {
  font-size: 1.25em;
  font-weight: 300;
}

.-title-call {
  margin: 0;
  font-weight: 300;
  opacity: .75;
  font-size: 1.25em;
}

body {
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
  color: #333333;
}

*:focus {
  outline: none;
}

a {
  text-decoration: none;
  color: inherit;
}

input:placeholder-shown {
  font-style: italic;
  color: #737373;
}

.cl-flex-row {
  display: flex;
  align-items: center;
}

.cl-flex-row > * {
  flex-grow: 1;
}

.cl-flex-row > * + * {
  margin-left: 15px;
}

.cl-flex-row + .cl-flex-row {
  margin-top: 15px;
}

@media (max-width: 768px) {
  .cl-flex-row:not(.--flex-only-row) {
    flex-direction: column;
  }
  .cl-flex-row:not(.--flex-only-row) > * + * {
    margin-left: 0;
    margin-top: 15px;
  }
}

@media (max-width: 768px) {
  .-mobile-block {
    display: block;
    width: 100%;
    margin-left: 0 !important;
    margin-right: 0 !important;
  }
  .-mobile-block + .-mobile-block {
    margin-top: 15px;
  }
}

.-bg-grey {
  background: #f2f2f2;
}

.-bg-main-color {
  background: #21b9a0;
}

.-txt-white {
  color: #fff;
}

.-txt-light-grey {
  color: #9e9e9e;
}

.-txt-main-color {
  color: #21b9a0;
}

.-default-anchor {
  font-weight: 600;
}

.-hide {
  display: none;
}

.cl-btn {
  border: none;
  padding: 12px 20px;
  border-radius: 5px;
  min-width: 160px;
  cursor: pointer;
  transition: .25s ease;
  display: inline-block;
  text-align: center;
}

.cl-btn.-cl-btn-main {
  background: #21b9a0;
  color: #fff;
}

.cl-btn.-cl-btn-main:hover {
  background: #198e7b;
}

.cl-btn.-cl-btn-additional {
  background: #b9213a;
  color: #fff;
}

.cl-btn.-cl-btn-additional:hover {
  background: #8e192c;
}

.cl-btn.-cl-btn-grey {
  background: #f2f2f2;
}

.cl-btn.-cl-btn-grey:hover {
  background: #d9d9d9;
}

.cl-btn + .cl-btn {
  margin-left: 10px;
}

.cl-btn.-cl-btn-secondary {
  color: #b3b3b3;
  box-shadow: inset 0px 0px 0px 1px #cccccc;
  box-sizing: border-box;
}

.cl-btn.-cl-btn-secondary:hover {
  color: #fff;
  background: #198e7b;
  box-shadow: inset 0px 0px 0px 1px #198e7b;
}

