  background: #21b9a0;
}

.cl-modal .input-field .datepicker-dropdown {
  padding: 10px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.3333);
  z-index: 20;
}

.datepicker-dropdown thead tr th {
  font-weight: 600;
}

.datepicker-dropdown thead {
  margin-bottom: 15px;
}

._cl-datatable {
  margin: 1rem 0 0 0;
}

._cl-datatable ._datatable {
  margin: 1rem 0;
  border-bottom: none !important;
}

._cl-datatable .dataTables_filter {
  display: none;
}

._cl-datatable table tr td:first-child {
  padding-left: 5px;
}

._cl-datatable table tr td:last-child {
  padding-right: 0;
}

._cl-datatable table tr td {
  border-bottom: 1px solid #d9d9d9;
}

._cl-datatable ._cl-datatable-pagenation a {
  padding: 6px 9px;
  border-radius: 3px;
  background: #f2f2f2;
  transition: .25s ease;
}

._cl-datatable ._cl-datatable-pagenation a + a {
  margin-left: 5px;
}

._cl-datatable ._cl-datatable-pagenation a:hover {
  background: #d9d9d9;
}

._cl-datatable ._cl-datatable-pagenation .current {
  background: #21b9a0;
  color: #fff;
  cursor: default;
}

._cl-datatable ._cl-datatable-pagenation .current:hover {
  background: #21b9a0;
}

._cl-datatable ._cl-datatable-pagenation span {
  margin: 0 5px;
}

.input-field {
  position: relative;
  width: 100%;
}

.input-field .label-placeholder {
  font-size: .65em;
  position: absolute;
  top: 5px;
  left: 10px;
  opacity: .75;
  z-index: 5;
  pointer-events: none;
}

.input-field > input,
.input-field > .select2-container {
  width: 100% !important;
  background: #f2f2f2;
  border: none;
  padding: 20px 10px 4px 10px;
  border-radius: 5px;
  border-bottom: 1px solid #cccccc;
  transition: .25s ease;
}

.input-field > input,
.input-field > .select2-container,
.input-field > label {
  cursor: pointer;
}

.input-field > input:focus,
.input-field > .select2-container.select2-container--open {
  border-bottom: 1.5px solid #cccccc;
}

.input-field > .select2-container {
  padding: 15px 5px 0px 3px;
}

.input-field > .select2-container .select2-selection {
  background: none;
  border: none;
}

.input-field .select2-container--default .select2-selection--single .select2-selection__arrow {
  top: 10px;
}

.cl-modal {
  position: fixed;
  overflow: auto;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.5);
  padding: 0 1rem;
  overflow-y: scroll;
}

.cl-modal .cl-modal-dialog {
  width: 100%;
  max-width: 800px;
  margin: 1.75rem auto;
  background: #fff;
  border-radius: 3px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.3333);
}

.cl-modal .cl-modal-dialog .cl-modal-content .cl-screen {
  border-radius: 3px;
}

.cl-modal .cl-modal-dialog .cl-modal-content .cl-screen > * + * {
  border-top: 1px solid #d9d9d9;
}

.cl-modal .cl-modal-dialog .cl-modal-content .cl-screen .cl-modal-header,
.cl-modal .cl-modal-dialog .cl-modal-content .cl-screen .cl-modal-body,
.cl-modal .cl-modal-dialog .cl-modal-content .cl-screen .cl-modal-actions {
  padding: 1.25rem;
}

.cl-modal .cl-modal-dialog .cl-modal-content .cl-screen .cl-modal-header .cl-title, .cl-modal .cl-modal-dialog .cl-modal-content .cl-screen .cl-modal-header p,
.cl-modal .cl-modal-dialog .cl-modal-content .cl-screen .cl-modal-body .cl-title,
.cl-modal .cl-modal-dialog .cl-modal-content .cl-screen .cl-modal-body p,
.cl-modal .cl-modal-dialog .cl-modal-content .cl-screen .cl-modal-actions .cl-title,
.cl-modal .cl-modal-dialog .cl-modal-content .cl-screen .cl-modal-actions p {
  margin-top: 0;
  margin-bottom: 0;
}

.cl-modal .cl-modal-dialog .cl-modal-content .cl-screen .cl-modal-body .cl-modal-block + .cl-modal-block {
  margin-top: 1.5rem;
}

.cl-modal .cl-modal-dialog .cl-modal-helpers {
  font-size: .75em;
  padding: 1rem 1.25rem 1rem 1.25rem;
  border-top: 1px solid #d9d9d9;
}

.cl-modal .input-field {
  position: relative;
}

.cl-modal .input-field .datepicker-dropdown {
  position: absolute;
  background: #fff;
}

.cl-screen {
  display: none;
}

.cl-screen.is-active {
  display: block;
}

.select2-container--default .select2-results__option--highlighted[aria-selected] {
  background: #21b9a0;
}
